"use client";
import Image from "next/image";
import Link from "next/link";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import "swiper/css";

const legacyData = {
  title:
    "SRI SUTTUR MATH THE <span class='text-primary'>1000-YEAR LEGACY</span>",
  subtitle: "ABOUT JSS UNIVERSITY",
  description:
    "Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa.",
  chancellor: {
    img: "/images/chancellor.png",
    name: "Jagadguru Sri Shivarathri Deshikendra Mahaswamiji",
    message: "Message From Chancellor",
  },
  highlights: [
    {
      number: "#37",
      text: "#201-300 IN ENGINEERING IN 2024",
      source: "Outlook 2020",
    },
    {
      number: "#9",
      text: "AMONG TOP EMERGING PRIVATE INSTITUTIONS 2016 BY",
      source: "Times of India",
    },
  ],
  buttons: [
    { text: "360 VIEW", link: "#" },
    { text: "WHY JSS", link: "#" },
    { text: "APPLY NOW", link: "#" },
  ],
  accreditations: [
    {
      name: "UGC",
      img: "/images/logos/ugc.png",
      text: "JSS Academy of Technical Education, Noida is recognized under UGC",
    },
    {
      name: "AICTE",
      img: "/images/logos/aicte.png",
      text: "Approved by All India Council for Technical Education (AICTE)",
    },
    {
      name: "NAAC",
      img: "/images/logos/naac.png",
      text: "National Assessment and Accreditation Council (NAAC)",
    },
  ],
};

export default function LegacySection() {
  return (
    <section className="container-fluid py-5 bg-light">
      <div className="container">
        <div className="mb-3">
          <p className="fw-bold small text-uppercase">{legacyData.subtitle}</p>
          <h2
            className="fw-bold display-6"
            dangerouslySetInnerHTML={{ __html: legacyData.title }}
          />
        </div>

        <div className="row align-items-center g-4">
          {/* Chancellor Image */}
          <div className="col-lg-5">
            <div className="position-relative">
              <Image
                src={legacyData.chancellor.img}
                alt={legacyData.chancellor.name}
                width={500}
                height={350}
                className="img-fluid rounded"
              />
              {/* Play Button Overlay */}
              <button
                className="btn btn-warning rounded-circle position-absolute top-50 start-50 translate-middle"
                style={{ width: "50px", height: "50px" }}
              >
                â–¶
              </button>
              <div className="bg-dark text-white p-2 mt-2 rounded small">
                <strong>{legacyData.chancellor.message}</strong>
                <br />
                {legacyData.chancellor.name}
              </div>
            </div>
          </div>

          {/* Right Content */}
          <div className="col-lg-7">
            <p className="text-muted">{legacyData.description}</p>

            {/* Highlight Boxes */}
            <div className="row g-3 mb-4">
              {legacyData.highlights.map((h, i) => (
                <div
                  key={i}
                  className="col-md-6 border p-3 rounded text-center"
                >
                  <h4 className="fw-bold text-danger">{h.number}</h4>
                  <p className="mb-1">{h.text}</p>
                  <small className="text-muted">{h.source}</small>
                </div>
              ))}
            </div>

            {/* Buttons */}
            <div className="d-flex gap-3">
              {legacyData.buttons.map((btn, i) => (
                <Link key={i} href={btn.link} className="btn btn-dark">
                  {btn.text}
                </Link>
              ))}
            </div>
          </div>
        </div>

        {/* Accreditation Logos Slider */}
        <div className="mt-5 pt-4 border-top">
          <Swiper
            modules={[Autoplay]}
            autoplay={{ delay: 3000 }}
            loop={true}
            spaceBetween={30}
            slidesPerView={3}
            breakpoints={{
              0: { slidesPerView: 1 },
              576: { slidesPerView: 2 },
              992: { slidesPerView: 3 },
            }}
          >
            {legacyData.accreditations.map((acc, i) => (
              <SwiperSlide key={i}>
                <div className="text-center px-3">
                  <Image
                    src={acc.img}
                    alt={acc.name}
                    width={80}
                    height={80}
                    className="img-fluid mb-2"
                  />
                  <p className="small">{acc.text}</p>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>
    </section>
  );
}
